html, body, body div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, figure, footer, header, menu, nav, section, time, mark, audio, video, details, summary {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
  background: transparent;
}

/* フォーカス、 アクティブ状態の装飾のカスタマイズ */
.input:active,
.textarea:active,
.input:focus,
.textarea:focus,
.input.is-active,
.is-active.textarea {
  border-color: #990000;
  outline: none;
}

/* ボタンクラスを定義していないaタグ */
.content a:not(.button) {
  border-bottom: 1px solid #69707a;
}

.content a:not(.button):visited {
  color: inherit;
}

.content a:not(.button):hover {
  color: #990000;
  border-bottom-color: #990000;
}

/* ボタンに関するカスタマイズ */
.button:hover,
.button:focus,
.button.is-active {
  color: #fff;
}

.textarea {
  font-size: 16px;
  line-height: 1.8em;
  height: auto;
}

.select {
  line-height: 1;
}

.select select {
  font-size: 18px;
  height: 40px;
  padding: 0 50px;
  width: 100%;
}

.select option {
  font-size: 18px;
}

.select:after {
  /* bulma 設定をいったん初期化 */
  border: inherit;
  border-right: inherit;
  border-top: inherit;
  content: inherit;
  display: inherit;
  height: inherit;
  pointer-events: none;
  position: inherit;
  -webkit-transform: inherit;
  -ms-transform: inherit;
  transform: inherit;
  width: inherit;
  margin-top: inherit;
  right: inherit;
  top: inherit;
  /* 再設定 */
  position: absolute;
  content: "\f0d7";
  font-family: FontAwesome;
  font-size: 22px;
  top: 10px;
  right: 10px;
  color: #666;
}

.table thead th {
  color: inherit;
}

.table tr:hover {
  background-color: #f5f7fa;
  color: inherit;
}

.button {
  font-size: 18px;
  line-height: 18px;
  margin: 0 auto;
}

input[type="checkbox"] {
  display: none;
}

input[type="checkbox"] + label {
  position: relative;
  display: inline-block;
  padding: 3px 3px 3px 50px !important;
  cursor: pointer;
  font-size: 20px;
  min-height: 40px;
  line-height: 40px;
  font-weight: bold;
}

input[type="checkbox"] + label:before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  width: 36px;
  height: 36px;
  margin-top: -18px;
  background: #fff;
  border: 1px solid #ccc;
}

input[type="checkbox"]:checked + label:after {
  position: absolute;
  content: url("../img/icon-check-32.png");
  top: 3px;
  left: 5px;
  width: 35px;
  height: 15px;
  /*
  border-left: 5px solid #3498db;
  border-bottom: 5px solid #3498db;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
*/
}

.title {
  color: inherit;
  font-size: inherit;
  line-height: 1;
}

.nav-item a:hover, a.nav-item:hover {
  color: inherit;
}

.checkbox:hover {
  color: #990000;
}

.select select:active,
.select select:focus,
.select select:hover,
.select select.is-active {
  border-color: #006ebb;
  outline: none;
}

.nav-left {
  overflow: inherit;
  overflow-x: inherit;
}

