/**
 *
 * jquery datepicker カスタムクラス
 * jquery datepicker(カレンダー)の表示イメージ
 * 変更用CSS定義
 *
 * @author yanahiro / Media Magic Co.,Ltd.
 * @since 2016.12.22
 *
**/
.ui-datepicker {
  width: inherit;
  padding: .2em .2em 0;
  display: none;
}

.ui-datepicker * {
  font-size: 20px;
  font-family: helvetica, arial, 'ヒラギノ角ゴシック Pro', 'Hiragino Kaku Gothic Pro', 'Meiryo UI', メイリオ, Meiryo, Osaka, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif !important;
}

.ui-datepicker table {
  width: 100%;
  font-size: .9em;
  border-collapse: collapse;
  margin: 0 0 .4em;
}

.ui-datepicker table th, .ui-datepicker table td {
  margin: 3px;
}

.ui-datepicker th {
  padding: .8em .8em;
  text-align: center;
  font-weight: bold;
  border: 0;
}

.ui-widget-content {
  border: 1px solid #ddd;
  background-color: #fff;
  color: #808080;
}

.ui-widget-header {
  border: 1px solid #ddd;
  background: #808080;
  color: #fff;
  font-weight: bold;
}

.ui-widget.ui-widget-content {
  border: 1px solid #c5c5c5;
}

.ui-datepicker td span, .ui-datepicker td a {
  display: block;
  padding: .7em;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
  text-decoration: none;
}

.ui-state-default, .ui-widget-content .ui-state-default, .ui-widget-header .ui-state-default, .ui-button, html .ui-button.ui-state-disabled:hover, html .ui-button.ui-state-disabled:active {
  border: 1px solid #c5c5c5;
  background: #f2f2f2;
  font-weight: normal;
  color: #454545;
}

.ui-widget-header .ui-icon {
  background-image: url("../img/ui-icons_ffffff_256x240.png");
}

.class-sunday > .ui-state-default {
  background: #cccccc !important;
  color: #990000 !important;
}

.class-saturday > .ui-state-default {
  background: #cccccc !important;
  color: #006ebb !important;
}

.ui-datepicker-calendar th:first-child {
  color: #990000;
}

.ui-datepicker-calendar th:last-child {
  color: #006ebb;
}

.ui-icon-circle-triangle-w {
  background-position: 0px 0px;
  background-repeat: no-repeat !important;
  background-size: contain !important;
  margin-right: 5px;
  vertical-align: middle;
  background-image: url(../img/icon/ic-angle-left.png) !important;
  width: 24px;
  height: 24px;
  line-height: 24px;
}

.ui-icon-circle-triangle-e {
  background-position: 0px 0px;
  background-repeat: no-repeat !important;
  background-size: contain !important;
  margin-right: 5px;
  vertical-align: middle;
  background-image: url(../img/icon/ic-angle-right.png) !important;
  width: 24px;
  height: 24px;
  line-height: 24px;
}

.ui-datepicker .ui-datepicker-prev span, .ui-datepicker .ui-datepicker-next span {
  display: block;
  position: absolute;
  left: 50%;
  margin-left: -13px;
  top: 50%;
  margin-top: -13px;
}

.ui-state-highlight, .ui-widget-content .ui-state-highlight, .ui-widget-header .ui-state-highlight {
  border: 1px solid #dad55e;
  background: #fffa90;
  color: #777620;
}

